import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/contacts/',
    name: 'Contacts',
    component: () => import('../views/Contacts.vue')
  },
  {
    path: '/guides/asm/',
    name: 'ASM Guides',
    component: () => import('../views/ASMGuides.vue')
  },
  {
    path: '/guides/asm/lab1/',
    name: 'First ASM Guide',
    component: () => import('../views/ASMGuide1.vue')
  },
  {
    path: '/guides/asm/prerequisites/',
    name: 'Prerequisites',
    component: () => import('../views/ASMGuide0.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})

export default router
